






























import Vue, { PropType } from 'vue';
import { MessageEventBus } from '@/libs/EventBus';
import { QuizGroupData } from '@/apis/models/QuizGroupModel';

export default Vue.extend({
  name: 'QuizGroupCard',
  components: {
  },
  props: {
    group: {
      type: Object as PropType<QuizGroupData>,
      required: false,
      default: null as QuizGroupData | null,
    },
  },
  data() {
    return {
      defaultThumbnial:
      'https://images.unsplash.com/photo-1605522220576-f5566d005f1c?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60',
      copyMessageDisplay: false,
      copyMessageTimeoutId: 0,
    };
  },
  computed: {
    previewImage(): string {
      if (this.group.previewImage) {
        return `${this.group.previewImage}/240`;
      }
      return this.defaultThumbnial;
    },
    dialogId(): string {
      return this.group.shortId;
    },
  },
  mounted() {
    MessageEventBus.$on('clear-copied-message', this.clearCopyMessageTimeout);
  },
  methods: {
    enterGroup() {
      this.$store.commit('changeQuizGroup', this.group);
      this.$router.push('/manager/quizgroup-maker');
    },
    removeGroup() {
      this.openRemoveModal();
    },
    openRemoveModal() {
      this.$modal.show('dialog', {
        title: '確定刪除嗎?',
        text: '這個群組刪掉就不會再回來了',
        buttons: [
          {
            title: '取消',
            handler: () => {
              this.$modal.hide('dialog');
            },
          },
          {
            title: '確定',
            handler: () => {
              this.$modal.hide('dialog');
              this.$emit('removeMe');
            },
          },
        ],
      });
    },
    copyGroupCode() {
      MessageEventBus.$emit('clear-copied-message');
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      this.$copyText(`q${this.group.shortId}`).then((e) => {
        this.copyMessageDisplay = true;
        this.copyMessageTimeoutId = setTimeout(this.clearCopyMessage, 1500);
      }, (e) => {
        console.error('複製失败', e.text);
      });
    },
    clearCopyMessage() {
      this.copyMessageDisplay = false;
    },
    clearCopyMessageTimeout() {
      this.clearCopyMessage();
      clearTimeout(this.copyMessageTimeoutId);
    },
  },
});
