import api from '@/apis/BaseApi';
import { QuizGroupData, QuizGroup, UpdateQuizGroupModel } from '@/apis/models/QuizGroupModel';
import { QuizInstance } from '@/models/QuizInstanceModel';

export default class QuizGroupApi {
  /**
   * Create a new QuizGroup for a workspace
   *
   * @param workspaceId Workspace to create a new QuizGroup
   */
  static async create(workspaceId: string): Promise<QuizGroupData> {
    const result = await api.post(`workspace/${workspaceId}/quiz-groups/`, {});
    return result.data.quizGroup;
  }

  /**
   * Remove a QuizGroup in a Workspace
   *
   * @param workspaceId Workspace to remove QuizGroup
   * @param quizGroupId QuizGroup to be removed
   */
  static async remove(workspaceId: string, quizGroupId: string): Promise<void> {
    const result = await api.delete(`workspace/${workspaceId}/quiz-groups/${quizGroupId}`);
    return result.data;
  }

  /**
   * Update QuizGroup by patch object
   *
   * @param workspaceId
   * @param quizGroupId
   * @param patch
   */
  static async update(workspaceId: string, quizGroupId: string, patch: UpdateQuizGroupModel): Promise<void> {
    const body = {
      ...patch,
    };

    await api.post(`workspace/${workspaceId}/quiz-groups/${quizGroupId}`, JSON.stringify(body));
  }

  /**
   * Get the detailed information of a quiz group
   * including all quizzes in the quiz group
   *
   * @param quizGroupId quiz group id
   */
  static async getQuizGroup(workspaceId: string, quizGroupId: string): Promise<QuizGroup> {
    const result = await api.get(`workspace/${workspaceId}/quiz-groups/${quizGroupId}`);
    return result.data;
  }

  /**
   * Get quiz groups of a workspace
   *
   * @param workspaceId workspace id
   */
  static async getQuizGroups(workspaceId: string): Promise<QuizGroupData[]> {
    const result = await api.get(`workspace/${workspaceId}/quiz-groups/`);
    return result.data.quizGroupList;
  }

  static async updateName(workspaceId: string, quizGroupId: string, name: string): Promise<void> {
    const patch = {
      name,
    } as UpdateQuizGroupModel;

    await QuizGroupApi.update(workspaceId, quizGroupId, patch);
  }

  private static prepareQuizIdList(quizList: QuizInstance[]): string[] {
    const quizIdList = [] as string[];
    quizList.forEach((quiz) => {
      quizIdList.push(quiz.uuid);
    });
    return quizIdList;
  }

  /**
   * Set quiz list for a QuizGroup.
   * This is not update original quiz list, but overwrite the origin one
   *
   * @param quizGroupId quiz group id
   * @param quizList quiz list to be set for the QuizGroup
   */
  // deprecated, replaced by patch
  static async setQuizList(workspaceId: string, quizGroupId: string, quizList: QuizInstance[]): Promise<void> {
    const quizIdList = this.prepareQuizIdList(quizList);

    const patch = {
      quizIdList,
    } as UpdateQuizGroupModel;

    await QuizGroupApi.update(workspaceId, quizGroupId, patch);
  }

  static async test() {
    const result = await api.post('quiz/test', {});
    console.log(result.data);
    return result.data;
  }
}
